import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import { useLocalizedData } from '@/composables/useLocalizedData'
import { fallbackSettings } from '@/data/site-settings'
import type { SiteSettings } from '@/types'

export interface SocialLink {
  url: string
  label: string
  icon: string
  enabled: boolean
  displayOrder: number
}

export interface ContactInfo {
  value: string
  label: string
  icon: string
  enabled: boolean
  type: 'email' | 'tel' | 'text'
}

export interface CTATexts {
  fr: {
    primary: string
    secondary: string
    followMe: string
  }
  en: {
    primary: string
    secondary: string
    followMe: string
  }
}

export interface SocialLinksState {
  socialMedia: Record<string, SocialLink>
  contact: Record<string, ContactInfo>
  cta: {
    contact: {
      enabled: boolean
      texts: CTATexts
    }
    resume: {
      enabled: boolean
      url: string
      texts: {
        fr: {
          download: string
          view: string
        }
        en: {
          download: string
          view: string
        }
      }
    }
  }
  footer: {
    copyright: {
      enabled: boolean
      texts: {
        fr: string
        en: string
      }
    }
    madeWith: {
      enabled: boolean
      texts: {
        fr: string
        en: string
      }
    }
  }
}

export const useSocialLinksStore = defineStore('socialLinks', () => {
  const { createLocalizedRef } = useLocalizedData()

  // Utiliser createLocalizedRef pour les données réactives
  const settingsData = createLocalizedRef<SiteSettings>('settings', fallbackSettings)
  const siteFooterData = createLocalizedRef<unknown>('site-footer', {})

  const loading = ref(false)
  const error = ref<string | null>(null)

  // Computed pour combiner les données
  const data = computed((): SocialLinksState => {
    const defaultCTA = {
      contact: {
        enabled: false,
        texts: {
          fr: { primary: '', secondary: '', followMe: '' },
          en: { primary: '', secondary: '', followMe: '' },
        },
      },
      resume: {
        enabled: false,
        url: '',
        texts: { fr: { download: '', view: '' }, en: { download: '', view: '' } },
      },
    }

    const defaultFooter = {
      copyright: { enabled: false, texts: { fr: '', en: '' } },
      madeWith: { enabled: false, texts: { fr: '', en: '' } },
    }

    const settings = settingsData.value

    return {
      socialMedia: (settings?.socialMedia as Record<string, SocialLink>) || {},
      contact:
        ((siteFooterData.value as Record<string, unknown>)?.contact as Record<
          string,
          ContactInfo
        >) || {},
      cta: Object.assign(
        {},
        defaultCTA,
        ((siteFooterData.value as Record<string, unknown>)?.cta as typeof defaultCTA) || {}
      ),
      footer: Object.assign(
        {},
        defaultFooter,
        ((siteFooterData.value as Record<string, unknown>)?.footer as typeof defaultFooter) || {}
      ),
    }
  })

  // Computed pour les liens sociaux activés et triés
  const enabledSocialLinks = computed(() => {
    return Object.entries(data.value.socialMedia)
      .filter(([_, link]) => link.enabled)
      .sort((a, b) => a[1].displayOrder - b[1].displayOrder)
      .map(([key, link]) => ({ key, ...link }))
  })

  // Computed pour les informations de contact activées
  const enabledContactInfo = computed(() => {
    return Object.entries(data.value.contact)
      .filter(([_, info]) => info.enabled)
      .map(([key, info]) => ({ key, ...info }))
  })

  // Computed pour obtenir l'URL de contact principal (email)
  const primaryContactUrl = computed(() => {
    const email = data.value.contact?.email
    if (email?.enabled) {
      return `mailto:${email.value}`
    }

    // Fallback vers le premier contact activé
    const firstContact = enabledContactInfo.value[0]
    if (firstContact) {
      switch (firstContact.type) {
        case 'email':
          return `mailto:${firstContact.value}`
        case 'tel':
          return `tel:${firstContact.value}`
        default:
          return '#'
      }
    }

    return '#'
  })

  // Fonction pour obtenir les textes selon la langue
  const getTexts = (locale: string = 'fr') => {
    const lang = locale === 'en' || locale === 'fr' ? locale : 'fr'
    return {
      contact: data.value.cta.contact.texts[lang],
      resume: data.value.cta.resume.texts[lang],
      footer: {
        copyright: data.value.footer.copyright.texts[lang],
        madeWith: data.value.footer.madeWith.texts[lang],
      },
    }
  }

  // Fonction pour mettre à jour un lien social
  const updateSocialLink = (key: string, updates: Partial<SocialLink>) => {
    if (data.value.socialMedia[key]) {
      data.value.socialMedia[key] = {
        ...data.value.socialMedia[key],
        ...updates,
      }
    }
  }

  // Fonction pour mettre à jour les informations de contact
  const updateContactInfo = (key: string, updates: Partial<ContactInfo>) => {
    if (data.value.contact[key]) {
      data.value.contact[key] = {
        ...data.value.contact[key],
        ...updates,
      }
    }
  }

  // Fonction pour activer/désactiver un lien social
  const toggleSocialLink = (key: string) => {
    if (data.value.socialMedia[key]) {
      data.value.socialMedia[key].enabled = !data.value.socialMedia[key].enabled
    }
  }

  // Fonction pour réorganiser les liens sociaux
  const reorderSocialLinks = (key: string, newOrder: number) => {
    if (data.value.socialMedia[key]) {
      data.value.socialMedia[key].displayOrder = newOrder
    }
  }

  return {
    // State
    data,
    loading,
    error,

    // Computed
    enabledSocialLinks,
    enabledContactInfo,
    primaryContactUrl,

    // Actions
    getTexts,
    updateSocialLink,
    updateContactInfo,
    toggleSocialLink,
    reorderSocialLinks,
  }
})
