<template>
  <button
    ref="themeButton"
    @click="handleToggleTheme"
    :class="[
      'relative rounded-full p-4 transition-all duration-500 ease-out',
      'overflow-hidden bg-gradient-to-r',
      isDark
        ? 'from-orange-400 to-yellow-400 shadow-lg shadow-orange-500/25'
        : 'from-indigo-500 to-purple-600 shadow-lg shadow-indigo-500/25',
      'hover:scale-110 hover:shadow-xl',
      'focus:border-0 focus:outline-none focus:ring-0',
      'transform-gpu',
    ]"
    :aria-label="isDark ? 'Activer le thème clair' : 'Activer le thème sombre'"
    :title="isDark ? 'Activer le thème clair' : 'Activer le thème sombre'"
  >
    <!-- Background Animation -->
    <div
      ref="backgroundElement"
      :class="[
        'absolute inset-0 rounded-full transition-all duration-700',
        'bg-gradient-to-r',
        isDark ? 'from-yellow-300 to-orange-400' : 'from-purple-500 to-indigo-600',
      ]"
    ></div>

    <!-- Icon Container -->
    <div class="absolute inset-0 z-10 flex items-center justify-center">
      <!-- Sun Icon -->
      <Transition
        name="sun"
        enter-active-class="transition-all duration-200 ease-in"
        enter-from-class="opacity-0 scale-0"
        enter-to-class="opacity-100 scale-100"
        leave-active-class="transition-all duration-100 ease-out"
        leave-from-class="opacity-100 scale-100"
        leave-to-class="opacity-0 scale-0"
      >
        <!-- SVG Sun Icon -->
        <svg
          v-if="isDark"
          class="h-6 w-6 transform-gpu text-white"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
          style="filter: drop-shadow(0 0 8px rgba(255, 255, 255, 0.8))"
        >
          <circle cx="12" cy="12" r="5"></circle>
          <line x1="12" y1="1" x2="12" y2="3"></line>
          <line x1="12" y1="21" x2="12" y2="23"></line>
          <line x1="4.22" y1="4.22" x2="5.64" y2="5.64"></line>
          <line x1="18.36" y1="18.36" x2="19.78" y2="19.78"></line>
          <line x1="1" y1="12" x2="3" y2="12"></line>
          <line x1="21" y1="12" x2="23" y2="12"></line>
          <line x1="4.22" y1="19.78" x2="5.64" y2="18.36"></line>
          <line x1="18.36" y1="5.64" x2="19.78" y2="4.22"></line>
        </svg>
      </Transition>

      <!-- Moon Icon -->
      <Transition
        name="moon"
        enter-active-class="transition-all duration-200 ease-in"
        enter-from-class="opacity-0 scale-0"
        enter-to-class="opacity-100 scale-100"
        leave-active-class="transition-all duration-100 ease-out"
        leave-from-class="opacity-100 scale-100"
        leave-to-class="opacity-0 scale-0"
      >
        <!-- SVG Moon Icon -->
        <svg
          v-if="!isDark"
          class="h-6 w-6 transform-gpu text-white"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
          style="filter: drop-shadow(0 0 8px rgba(255, 255, 255, 0.6))"
        >
          <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path>
        </svg>
      </Transition>
    </div>

    <!-- Ripple Effect Container -->
    <div
      ref="rippleContainer"
      class="pointer-events-none absolute inset-0 overflow-hidden rounded-full"
    ></div>

    <!-- Animated Border -->
    <div
      :class="[
        'absolute inset-0 rounded-full transition-all duration-500',
        'border-2',
        isDark ? 'border-orange-200/50' : 'border-indigo-200/50',
      ]"
    ></div>
  </button>
</template>

<script setup lang="ts">
import { ref } from 'vue'
import { useTheme } from '@/composables/useTheme'
import { useAdvancedAnimations } from '@/composables/useAdvancedAnimations'

const { isDark, toggleTheme } = useTheme()
const { createRippleEffect } = useAdvancedAnimations()

const themeButton = ref<HTMLElement>()
const backgroundElement = ref<HTMLElement>()
const rippleContainer = ref<HTMLElement>()

// Enhanced theme toggle with smooth animations
const handleToggleTheme = async (event: MouseEvent) => {
  // Create subtle ripple effect
  if (rippleContainer.value) {
    createRippleEffect(event, rippleContainer.value, 'rgba(255, 255, 255, 0.2)')
  }

  // Add smooth scale animation without rotation
  if (themeButton.value) {
    themeButton.value.style.transition = 'transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275)'
    themeButton.value.style.transform = 'scale(0.9)'

    setTimeout(() => {
      if (themeButton.value) {
        themeButton.value.style.transform = 'scale(1)'
      }
    }, 150)

    setTimeout(() => {
      if (themeButton.value) {
        themeButton.value.style.transform = ''
        themeButton.value.style.transition = ''
      }
    }, 300)
  }

  // Toggle theme immediately for instant icon change
  toggleTheme()
}
</script>

<style scoped>
/* Smooth button animations */
button {
  outline: none !important;
  border: none !important;
  border-radius: 50% !important;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

button:hover {
  transform: scale(1.05);
}

button:focus {
  outline: none !important;
  border: none !important;
  border-radius: 50% !important;
}

button:active {
  outline: none !important;
  border: none !important;
  border-radius: 50% !important;
  transform: scale(0.98);
}

button:focus-visible {
  outline: none !important;
  border: none !important;
  border-radius: 50% !important;
}

/* Icon transition improvements */
.sun-enter-from,
.moon-enter-from {
  opacity: 0;
  transform: scale(0);
}

.sun-enter-to,
.moon-enter-to {
  opacity: 1;
  transform: scale(1);
}

.sun-leave-from,
.moon-leave-from {
  opacity: 1;
  transform: scale(1);
}

.sun-leave-to,
.moon-leave-to {
  opacity: 0;
  transform: scale(0);
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  button {
    animation: none !important;
  }

  .sun-enter-active,
  .sun-leave-active,
  .moon-enter-active,
  .moon-leave-active {
    transition-duration: 0.01ms !important;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  button {
    outline: 2px solid;
    outline-offset: 2px;
  }
}
</style>
