import { createI18n } from 'vue-i18n'

// Messages vides - les traductions seront chargées dynamiquement depuis src/data
const messages = {
  fr: {},
  en: {},
}

// Fonction pour obtenir la langue du navigateur
function getBrowserLanguage(): string {
  const browserLang = navigator.language?.split('-')[0]
  const supportedLanguages = ['fr', 'en']
  return supportedLanguages.includes(browserLang || '') ? browserLang || 'fr' : 'fr'
}

// Fonction pour obtenir la langue stockée ou détecter automatiquement
function getInitialLanguage(): string {
  if (typeof window === 'undefined') return 'fr'

  // Vérifier le localStorage
  const storedLang = localStorage.getItem('portfolio-language')
  if (storedLang && ['fr', 'en'].includes(storedLang)) {
    return storedLang
  }

  // Détecter la langue du navigateur
  return getBrowserLanguage()
}

// Configuration i18n
export const i18n = createI18n({
  legacy: false,
  locale: getInitialLanguage(),
  fallbackLocale: 'fr',
  messages,
  globalInjection: true,
})

export default i18n
