import { createRouter, createWebHistory } from 'vue-router'

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  scrollBehavior(to, _from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else if (to.hash) {
      return { el: to.hash, behavior: 'smooth' }
    } else {
      return { top: 0, behavior: 'smooth' }
    }
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/pages/index.vue'),
      meta: {
        title: 'Portfolio - Développeur Full Stack',
        description: 'Développeur Full Stack passionné par les technologies modernes',
      },
    },
    {
      path: '/legal-notice',
      name: 'legal-notice',
      component: () => import('@/pages/legal-notice.vue'),
      meta: {
        title: 'Mentions légales - Portfolio',
        description: 'Mentions légales du site portfolio',
      },
    },
    {
      path: '/privacy-policy',
      name: 'privacy-policy',
      component: () => import('@/pages/privacy-policy.vue'),
      meta: {
        title: 'Politique de confidentialité - Portfolio',
        description: 'Politique de confidentialité du site portfolio',
      },
    },
    {
      path: '/:pathMatch(.*)*',
      name: '404',
      component: () => import('@/pages/404.vue'),
      meta: {
        title: 'Page non trouvée - Portfolio',
        description: "La page que vous cherchez n'existe pas",
      },
    },
  ],
})

// Navigation guards for SEO
router.beforeEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title as string
  }
})

export default router
