import { ref, onMounted } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

// Register GSAP plugins
if (typeof window !== 'undefined') {
  gsap.registerPlugin(ScrollTrigger)
}

export interface AnimationConfig {
  duration?: number
  ease?: string
  delay?: number
  stagger?: number
}

export interface ParticleConfig {
  count?: number
  colors?: string[]
  size?: { min: number; max: number }
  speed?: { min: number; max: number }
  opacity?: { min: number; max: number }
}

export interface ScrollAnimationOptions {
  threshold?: number
  rootMargin?: string
  once?: boolean
  duration?: number
  delay?: number
  easing?: string
}

export function useAdvancedAnimations() {
  const animationsActive = ref(true)
  const prefersReducedMotion = ref(false)

  // Check for reduced motion preference
  const checkReducedMotion = () => {
    if (typeof window !== 'undefined') {
      prefersReducedMotion.value = window.matchMedia('(prefers-reduced-motion: reduce)').matches
      animationsActive.value = !prefersReducedMotion.value
    }
  }

  // Morphing animation for avatar entrance
  const morphingEntrance = (element: HTMLElement, config: AnimationConfig = {}) => {
    if (!animationsActive.value) return

    const timeline = gsap.timeline()

    timeline
      .set(element, {
        scale: 0,
        opacity: 0,
        borderRadius: '50%',
      })
      .to(element, {
        scale: 1.2,
        opacity: 0.3,
        duration: 0.3,
        ease: 'power2.out',
      })
      .to(element, {
        scale: 1,
        opacity: 1,
        borderRadius: '50%',
        duration: 0.5,
        ease: 'elastic.out(1, 0.5)',
        ...config,
      })

    return timeline
  }

  // Advanced typewriter with multiple cursors
  const advancedTypewriter = async (
    elements: HTMLElement[],
    texts: string[],
    config: AnimationConfig = {}
  ) => {
    if (!animationsActive.value) {
      elements.forEach((el, i) => {
        if (el) el.textContent = texts[i] || ''
      })
      return
    }

    const { duration = 100, delay = 0 } = config

    await new Promise((resolve) => setTimeout(resolve, delay))

    for (let i = 0; i < elements.length; i++) {
      const element = elements[i]
      const text = texts[i] || ''

      if (element) {
        element.innerHTML = ''

        // Add cursor
        const cursor = document.createElement('span')
        cursor.className = 'typewriter-cursor'
        cursor.textContent = '|'
        element.appendChild(cursor)

        // Type each character
        for (let j = 0; j < text.length; j++) {
          await new Promise((resolve) => setTimeout(resolve, duration))
          element.insertBefore(document.createTextNode(text.charAt(j)), cursor)
        }

        // Remove cursor after a delay
        setTimeout(() => {
          if (cursor.parentNode) {
            cursor.parentNode.removeChild(cursor)
          }
        }, 1000)
      }
    }
  }

  // Holographic effect for skills
  const holographicEffect = (element: HTMLElement, _config: AnimationConfig = {}) => {
    if (!animationsActive.value) return

    const timeline = gsap.timeline({ repeat: -1, yoyo: true })

    timeline
      .to(element, {
        rotationY: 360,
        duration: 4,
        ease: 'none',
      })
      .to(
        element,
        {
          boxShadow: '0 0 30px rgba(0, 255, 255, 0.5)',
          duration: 2,
          ease: 'sine.inOut',
        },
        '<'
      )

    return timeline
  }

  // Ripple effect for buttons
  const createRippleEffect = (
    event: MouseEvent,
    element: HTMLElement,
    color = 'rgba(255, 255, 255, 0.3)'
  ) => {
    if (!animationsActive.value) return

    const rect = element.getBoundingClientRect()
    const size = Math.max(rect.width, rect.height)
    const x = event.clientX - rect.left - size / 2
    const y = event.clientY - rect.top - size / 2

    const ripple = document.createElement('span')
    ripple.style.cssText = `
      position: absolute;
      border-radius: 50%;
      background: ${color};
      transform: scale(0);
      animation: ripple 0.6s linear;
      left: ${x}px;
      top: ${y}px;
      width: ${size}px;
      height: ${size}px;
    `

    element.appendChild(ripple)

    setTimeout(() => {
      if (ripple.parentNode) {
        ripple.parentNode.removeChild(ripple)
      }
    }, 600)
  }

  // Smooth scroll with custom easing
  const smoothScrollTo = (target: string | HTMLElement, _config: AnimationConfig = {}) => {
    const element =
      typeof target === 'string' ? (document.querySelector(target) as HTMLElement) : target
    if (!element) return

    element.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
    })
  }

  // Staggered reveal animation
  const staggeredReveal = (elements: HTMLElement[], config: AnimationConfig = {}) => {
    if (!animationsActive.value || elements.length === 0) return

    const timeline = gsap.timeline()

    timeline.fromTo(
      elements,
      {
        y: 50,
        opacity: 0,
        scale: 0.9,
      },
      {
        y: 0,
        opacity: 1,
        scale: 1,
        duration: config.duration || 0.6,
        ease: config.ease || 'power2.out',
        stagger: config.stagger || 0.1,
      }
    )

    return timeline
  }

  // Parallax mouse tracking
  const parallaxMouse = (element: HTMLElement, intensity = 0.1) => {
    if (!animationsActive.value) return

    let isActive = false

    const handleMouseMove = (event: MouseEvent) => {
      if (!isActive) return

      const rect = element.getBoundingClientRect()
      const centerX = rect.left + rect.width / 2
      const centerY = rect.top + rect.height / 2

      const deltaX = (event.clientX - centerX) * intensity
      const deltaY = (event.clientY - centerY) * intensity

      gsap.to(element, {
        x: deltaX,
        y: deltaY,
        duration: 0.3,
        ease: 'power2.out',
      })
    }

    const handleMouseEnter = () => {
      isActive = true
      gsap.to(element, {
        scale: 1.05,
        duration: 0.3,
        ease: 'power2.out',
      })
    }

    const handleMouseLeave = () => {
      isActive = false
      gsap.to(element, {
        x: 0,
        y: 0,
        scale: 1,
        duration: 0.5,
        ease: 'elastic.out(1, 0.5)',
      })
    }

    element.addEventListener('mousemove', handleMouseMove)
    element.addEventListener('mouseenter', handleMouseEnter)
    element.addEventListener('mouseleave', handleMouseLeave)

    return () => {
      element.removeEventListener('mousemove', handleMouseMove)
      element.removeEventListener('mouseenter', handleMouseEnter)
      element.removeEventListener('mouseleave', handleMouseLeave)
    }
  }

  // Advanced counter animation with easing
  const animateCounter = (element: HTMLElement, target: number, config: AnimationConfig = {}) => {
    if (!animationsActive.value) {
      element.textContent = target.toString() + (target >= 10 ? '+' : '')
      return
    }

    // Ensure element starts at 0
    element.textContent = '0'

    // Create an object to animate
    const counterObj = { value: 0 }

    const timeline = gsap.timeline()

    timeline.to(counterObj, {
      value: target,
      duration: (config.duration || 2000) / 1000, // Convert ms to seconds for GSAP
      ease: config.ease || 'power2.out',
      onUpdate: function () {
        const currentValue = Math.floor(counterObj.value)
        element.textContent = currentValue.toString() + (target >= 10 ? '+' : '')
      },
    })

    return timeline
  }

  // Particle system manager
  const createParticleSystem = (container: HTMLElement, config: ParticleConfig = {}) => {
    if (!animationsActive.value) return

    const {
      count = 20,
      colors = ['#3B82F6', '#8B5CF6', '#EC4899'],
      size = { min: 2, max: 6 },
      speed = { min: 20, max: 40 },
      opacity = { min: 0.2, max: 0.6 },
    } = config

    const particles: HTMLElement[] = []

    for (let i = 0; i < count; i++) {
      const particle = document.createElement('div')
      const particleSize = Math.random() * (size.max - size.min) + size.min
      const particleColor = colors[Math.floor(Math.random() * colors.length)]
      const particleOpacity = Math.random() * (opacity.max - opacity.min) + opacity.min
      const particleSpeed = Math.random() * (speed.max - speed.min) + speed.min

      particle.style.cssText = `
        position: absolute;
        width: ${particleSize}px;
        height: ${particleSize}px;
        background: ${particleColor};
        border-radius: 50%;
        opacity: ${particleOpacity};
        pointer-events: none;
        left: ${Math.random() * 100}%;
        top: ${Math.random() * 100}%;
      `

      container.appendChild(particle)
      particles.push(particle)

      // Animate particle
      gsap.to(particle, {
        x: (Math.random() - 0.5) * 200,
        y: (Math.random() - 0.5) * 200,
        rotation: 360,
        duration: particleSpeed,
        repeat: -1,
        yoyo: true,
        ease: 'sine.inOut',
      })
    }

    return () => {
      particles.forEach((particle) => {
        if (particle.parentNode) {
          particle.parentNode.removeChild(particle)
        }
      })
    }
  }

  onMounted(() => {
    checkReducedMotion()

    // Listen for changes in motion preference
    if (typeof window !== 'undefined') {
      const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)')
      mediaQuery.addEventListener('change', checkReducedMotion)
    }
  })

  return {
    animationsActive,
    prefersReducedMotion,
    morphingEntrance,
    advancedTypewriter,
    holographicEffect,
    createRippleEffect,
    smoothScrollTo,
    staggeredReveal,
    parallaxMouse,
    animateCounter,
    createParticleSystem,
  }
}
