export const localizedSettings: Partial<Record<SupportedLocale, SiteSettings>> = {
  [FALLBACK_LOCALE]: fallbackSettings,
}

export function getSettingsForLocale(locale: string): SiteSettings | undefined {
  const normalizedLocale = SUPPORTED_LOCALES.includes(locale as SupportedLocale)
    ? (locale as SupportedLocale)
    : FALLBACK_LOCALE

  return localizedSettings[normalizedLocale] ?? localizedSettings[FALLBACK_LOCALE]
}
