<template>
  <component
    :is="component"
    :class="[
      'inline-flex items-center justify-center',
      sizeClasses,
      colorClasses,
      { 'animate-spin': loading },
    ]"
    v-bind="$attrs"
  >
    <FontAwesomeIcon v-if="isFontAwesome" :icon="iconArray" />
    <i v-else-if="name" :class="name" />
    <svg v-else-if="svg" v-html="svg" fill="currentColor" viewBox="0 0 24 24" />
    <slot v-else />
  </component>
</template>

<script setup lang="ts">
import { computed } from 'vue'

export interface IconProps {
  name?: string
  svg?: string
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl' | '2xl'
  color?: 'current' | 'primary' | 'secondary' | 'success' | 'warning' | 'danger' | 'muted'
  component?: string
  loading?: boolean
  // FontAwesome props
  prefix?: 'fas' | 'far' | 'fab' | 'fal' | 'fad'
}

const props = withDefaults(defineProps<IconProps>(), {
  size: 'md',
  color: 'current',
  component: 'span',
  loading: false,
  prefix: 'fas',
})

// Vérifier si c'est une icône FontAwesome
const isFontAwesome = computed(() => {
  return props.name && (props.name.startsWith('fa-') || props.prefix)
})

// Format pour FontAwesome
const iconArray = computed(() => {
  if (!props.name) return null

  // Si le nom contient déjà le préfixe, l'utiliser tel quel
  if (props.name.includes(' ')) {
    return props.name.split(' ')
  }

  // Sinon utiliser le préfixe fourni
  return [props.prefix, props.name.replace('fa-', '')]
})

// Classes de taille
const sizeClasses = computed(() => {
  const sizes = {
    xs: 'w-3 h-3 text-xs',
    sm: 'w-4 h-4 text-sm',
    md: 'w-5 h-5 text-base',
    lg: 'w-6 h-6 text-lg',
    xl: 'w-8 h-8 text-xl',
    '2xl': 'w-10 h-10 text-2xl',
  }
  return sizes[props.size]
})

// Classes de couleur
const colorClasses = computed(() => {
  const colors = {
    current: 'text-current',
    primary: 'text-primary-600 dark:text-primary-400',
    secondary: 'text-gray-600 dark:text-gray-400',
    success: 'text-green-600 dark:text-green-400',
    warning: 'text-yellow-600 dark:text-yellow-400',
    danger: 'text-red-600 dark:text-red-400',
    muted: 'text-gray-400 dark:text-gray-500',
  }
  return colors[props.color]
})
</script>
