const localizedContentByLocale: Record<SupportedLocale, Record<string, unknown>> = {
  fr: frContent,
  en: enContent,
}

type LocalizedSectionsMap = Record<string, Record<SupportedLocale, unknown>>

export const localizedContent: LocalizedSectionsMap = Object.freeze(
  buildLocalizedSections(localizedContentByLocale)
)

export const CONTENT_SECTION_KEYS = Object.freeze(Object.keys(localizedContent))

function buildLocalizedSections(
  locales: Record<SupportedLocale, Record<string, unknown>>
): LocalizedSectionsMap {
  const sectionKeys = new Set<string>()

  SUPPORTED_LOCALES.forEach((locale) => {
    const sections = locales[locale]
    if (!sections) {
      return
    }
    Object.keys(sections).forEach((sectionKey) => sectionKeys.add(sectionKey))
  })

  const result: LocalizedSectionsMap = {}

  sectionKeys.forEach((sectionKey) => {
    const sectionEntry = {} as Record<SupportedLocale, unknown>
    SUPPORTED_LOCALES.forEach((locale) => {
      const localeSections = locales[locale]
      if (!localeSections) {
        sectionEntry[locale] = null
        return
      }

      sectionEntry[locale] = localeSections[sectionKey] ?? null
    })

    result[sectionKey] = sectionEntry
  })

  return result
}
