<template>
  <nav
    ref="navbarElement"
    :class="[
      'fixed left-0 right-0 top-0 z-50 transition-all duration-500',
      'border-b backdrop-blur-md',
      isScrolled
        ? 'border-gray-200/50 bg-white/90 shadow-lg dark:border-gray-800/50 dark:bg-gray-900/90'
        : 'border-transparent bg-white/80 dark:bg-gray-900/80',
      'transform-gpu',
    ]"
  >
    <div class="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
      <div class="flex h-16 items-center justify-between">
        <!-- Enhanced Logo with animation -->
        <div class="flex items-center">
          <router-link
            to="/"
            ref="logoElement"
            class="group relative text-xl font-bold text-gray-900 transition-all duration-300 dark:text-white"
            @mouseenter="animateLogo"
          >
            <span class="relative z-10">{{ tUI('nav.portfolio') }}</span>
            <div
              class="absolute inset-0 -skew-x-12 bg-gradient-to-r from-blue-600 to-purple-600 opacity-0 transition-all duration-300 group-hover:opacity-10"
            ></div>
          </router-link>
        </div>

        <!-- Enhanced Navigation desktop with breadcrumb -->
        <div class="hidden md:block">
          <div class="relative ml-10 flex items-center space-x-1">
            <!-- Animated Background Indicator -->
            <div
              ref="navIndicator"
              :class="[
                'absolute h-10 bg-gradient-to-r from-blue-500/10 to-purple-500/10',
                'rounded-lg transition-all duration-300 ease-out',
                'border border-blue-500/20',
              ]"
              :style="indicatorStyle"
            ></div>

            <button
              v-for="(item, index) in navigationItems"
              :key="item.section"
              ref="navLinks"
              type="button"
              class="group relative cursor-pointer rounded-lg px-4 py-2 text-gray-700 transition-all duration-300 hover:text-blue-600 dark:text-gray-300 dark:hover:text-blue-400"
              @mouseenter="updateIndicator(index)"
              @click="setActiveSection(item.section)"
            >
              <span class="relative z-10">{{ item.label }}</span>

              <!-- Hover effect -->
              <div
                class="absolute inset-0 rounded-lg bg-gradient-to-r from-blue-500/5 to-purple-500/5 opacity-0 transition-opacity duration-300 group-hover:opacity-100"
              ></div>

              <!-- Active indicator -->
              <div
                v-if="activeSection === item.section"
                class="absolute bottom-0 left-1/2 h-1 w-1 -translate-x-1/2 transform animate-pulse rounded-full bg-blue-500"
              ></div>
            </button>
          </div>
        </div>

        <!-- Enhanced Actions -->
        <div class="flex items-center space-x-4">
          <!-- Language Toggle -->
          <LanguageToggle />

          <!-- Enhanced Theme toggle -->
          <ThemeToggle />

          <!-- Enhanced Mobile menu button -->
          <button
            ref="mobileMenuButton"
            @click="toggleMobileMenu"
            class="relative rounded-lg bg-gray-100/50 p-2 backdrop-blur-sm transition-all duration-300 hover:bg-gray-200/50 dark:bg-gray-800/50 dark:hover:bg-gray-700/50 md:hidden"
            aria-label="Menu mobile"
          >
            <!-- Animated hamburger -->
            <div class="relative flex h-6 w-6 flex-col justify-center">
              <span
                :class="[
                  'absolute h-0.5 w-6 bg-gray-600 transition-all duration-300 dark:bg-gray-400',
                  mobileMenuOpen ? 'translate-y-0 rotate-45' : '-translate-y-2',
                ]"
              ></span>
              <span
                :class="[
                  'absolute h-0.5 w-6 bg-gray-600 transition-all duration-300 dark:bg-gray-400',
                  mobileMenuOpen ? 'scale-0 opacity-0' : 'scale-100 opacity-100',
                ]"
              ></span>
              <span
                :class="[
                  'absolute h-0.5 w-6 bg-gray-600 transition-all duration-300 dark:bg-gray-400',
                  mobileMenuOpen ? 'translate-y-0 -rotate-45' : 'translate-y-2',
                ]"
              ></span>
            </div>
          </button>
        </div>
      </div>
    </div>

    <!-- Enhanced Navigation mobile with animations -->
    <Transition
      name="mobile-menu"
      enter-active-class="transition-all duration-300 ease-out"
      enter-from-class="opacity-0 max-h-0"
      enter-to-class="opacity-100 max-h-64"
      leave-active-class="transition-all duration-200 ease-in"
      leave-from-class="opacity-100 max-h-64"
      leave-to-class="opacity-0 max-h-0"
    >
      <div
        v-show="mobileMenuOpen"
        class="overflow-hidden border-t border-gray-200/50 bg-white/95 backdrop-blur-md dark:border-gray-800/50 dark:bg-gray-900/95 md:hidden"
      >
        <div class="space-y-1 px-2 pb-3 pt-2">
          <button
            v-for="(item, index) in navigationItems"
            :key="`mobile-${item.section}`"
            type="button"
            @click="setActiveSection(item.section)"
            :style="{ animationDelay: `${index * 50}ms` }"
            class="animate-slideInLeft block w-full cursor-pointer rounded-lg px-4 py-3 text-left text-gray-700 transition-all duration-300 hover:bg-gray-100/50 hover:text-blue-600 dark:text-gray-300 dark:hover:bg-gray-800/50 dark:hover:text-blue-400"
          >
            <span class="flex items-center">
              <i :class="item.icon" class="mr-3 w-4"></i>
              {{ item.label }}
            </span>
          </button>
        </div>
      </div>
    </Transition>

    <!-- Breadcrumb Trail -->
    <Transition
      name="breadcrumb"
      enter-active-class="transition-all duration-500"
      enter-from-class="opacity-0 transform translate-y-2"
      enter-to-class="opacity-100 transform translate-y-0"
    >
      <div
        v-if="showBreadcrumb && activeSection"
        class="border-t border-blue-200/30 bg-gradient-to-r from-blue-50/50 to-purple-50/50 px-4 py-2 dark:border-blue-800/30 dark:from-blue-900/20 dark:to-purple-900/20"
      >
        <div class="mx-auto max-w-7xl">
          <div class="flex items-center text-sm text-gray-600 dark:text-gray-400">
            <FontAwesomeIcon icon="home" class="mr-2" />
            <span>{{ tUI('nav.home') }}</span>
            <FontAwesomeIcon icon="chevron-right" class="mx-2 text-xs" />
            <span class="font-medium text-blue-600 dark:text-blue-400">{{
              getCurrentSectionLabel()
            }}</span>
          </div>
        </div>
      </div>
    </Transition>
  </nav>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted, computed } from 'vue'
import { useScrollRevealAnimations } from '@/composables/useScrollRevealAnimations'
import { useI18n } from '@/composables/useI18n'
import ThemeToggle from './ThemeToggle.vue'
import LanguageToggle from './LanguageToggle.vue'

const { smoothScrollTo } = useScrollRevealAnimations()
const { tUI } = useI18n()

// Template refs
const navbarElement = ref<HTMLElement>()
const logoElement = ref<HTMLElement>()
const navIndicator = ref<HTMLElement>()
const navLinks = ref<HTMLElement[]>([])
const mobileMenuButton = ref<HTMLElement>()

// State
const mobileMenuOpen = ref(false)
const isScrolled = ref(false)
const activeSection = ref('home')
const showBreadcrumb = ref(false)
const indicatorStyle = ref({
  width: '0px',
  left: '0px',
  opacity: '0',
})

// Navigation items with dynamic translations
const navigationItems = computed(() => [
  { section: 'projects', label: tUI('nav.projects'), icon: 'fas fa-folder' },
  { section: 'skills', label: tUI('nav.skills'), icon: 'fas fa-cogs' },
  { section: 'experience', label: tUI('nav.experience'), icon: 'fas fa-briefcase' },
  { section: 'testimonials', label: tUI('nav.testimonials'), icon: 'fas fa-quote-left' },
  { section: 'about', label: tUI('nav.about'), icon: 'fas fa-user' },
  { section: 'contact', label: tUI('nav.contact'), icon: 'fas fa-envelope' },
])

// Enhanced scroll handler
const handleScroll = () => {
  const scrollY = window.scrollY
  isScrolled.value = scrollY > 50

  // Update active section based on scroll position
  updateActiveSection()

  // Show/hide breadcrumb
  showBreadcrumb.value = scrollY > 200
}

// Update active section based on scroll position
const updateActiveSection = () => {
  const sections = ['home', 'projects', 'skills', 'experience', 'testimonials', 'about', 'contact']
  const scrollPosition = window.scrollY
  const windowHeight = window.innerHeight
  let foundActiveSection = false

  // Si on est vraiment tout en haut (moins de 50px), pas de section active
  if (scrollPosition < 50) {
    activeSection.value = ''
    return
  }

  for (const section of sections) {
    const element = document.getElementById(section)
    if (element) {
      const { offsetTop, offsetHeight } = element
      const viewportCenter = scrollPosition + windowHeight / 2

      // Considérer la section active si le centre de la viewport est dans la section
      if (viewportCenter >= offsetTop && viewportCenter < offsetTop + offsetHeight) {
        if (activeSection.value !== section) {
          activeSection.value = section
        }
        foundActiveSection = true
        break
      }
    }
  }

  // Si on n'est dans aucune section, enlever le highlight
  if (!foundActiveSection) {
    activeSection.value = ''
  }
}

// Enhanced mobile menu toggle
const toggleMobileMenu = () => {
  mobileMenuOpen.value = !mobileMenuOpen.value
}

const closeMobileMenu = () => {
  mobileMenuOpen.value = false
}

// Enhanced navigation indicator
const updateIndicator = (index: number) => {
  const linkElement = navLinks.value[index]
  if (linkElement && navIndicator.value) {
    // Vérifier si l'élément a la méthode getBoundingClientRect
    const element = (linkElement as HTMLElement & { $el?: HTMLElement }).$el || linkElement
    if (element && typeof element.getBoundingClientRect === 'function') {
      const rect = element.getBoundingClientRect()
      const navRect = navIndicator.value.parentElement?.getBoundingClientRect()

      if (navRect) {
        indicatorStyle.value = {
          width: `${rect.width}px`,
          left: `${rect.left - navRect.left}px`,
          opacity: '1',
        }
      }
    }
  }
}

const setActiveSection = (section: string) => {
  activeSection.value = section
  closeMobileMenu()

  // Smooth scroll to section using the same logic as CTA buttons
  smoothScrollTo(`#${section}`)
}

// Logo animation
const animateLogo = () => {
  if (logoElement.value) {
    logoElement.value.style.transform = 'scale(1.05) rotate(2deg)'
    setTimeout(() => {
      if (logoElement.value) {
        logoElement.value.style.transform = ''
      }
    }, 300)
  }
}

// Get current section label
const getCurrentSectionLabel = () => {
  if (!activeSection.value) return tUI('nav.home')
  const item = navigationItems.value.find((item) => item.section === activeSection.value)
  return item?.label || tUI('nav.home')
}

// Reset indicator on mouse leave
const resetIndicator = () => {
  indicatorStyle.value = { ...indicatorStyle.value, opacity: '0' }
}

onMounted(() => {
  window.addEventListener('scroll', handleScroll, { passive: true })
  handleScroll() // Initial call

  // Set up indicator reset on nav container mouse leave
  const navContainer = navIndicator.value?.parentElement
  if (navContainer) {
    navContainer.addEventListener('mouseleave', resetIndicator)
  }
})

onUnmounted(() => {
  window.removeEventListener('scroll', handleScroll)

  const navContainer = navIndicator.value?.parentElement
  if (navContainer) {
    navContainer.removeEventListener('mouseleave', resetIndicator)
  }
})
</script>

<style scoped>
/* Mobile menu animations */
.mobile-menu-enter-active .animate-slideInLeft {
  animation: slideInLeft 0.3s ease-out forwards;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Enhanced navbar scroll effect */
nav {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Logo hover effect */
.group:hover span {
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Enhanced focus states */
a:focus,
button:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
  border-radius: 0.5rem;
}

/* Navigation indicator animation */
.nav-indicator {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Breadcrumb animations */
.breadcrumb-enter-active {
  transition-delay: 200ms;
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  nav {
    border-bottom: 2px solid;
  }

  a,
  button {
    border: 1px solid;
  }
}

/* Mobile optimizations */
@media (max-width: 768px) {
  .animate-slideInLeft {
    animation-duration: 0.2s;
  }
}
</style>
