import { ref, computed, watch } from 'vue'
import { useI18n as useVueI18n } from 'vue-i18n'
import { useLocalizedData } from '@/composables/useLocalizedData'

// Cache pour les données UI
const uiData = ref<Record<string, unknown> | null>(null)
const isUILoaded = ref(false)

/**
 * Composable pour l'internationalisation de l'interface utilisateur
 * Utilise les données de ui.json pour les traductions des éléments de l'interface
 */
export function useI18n() {
  const { locale } = useVueI18n()
  const { loadUIData } = useLocalizedData()

  // Charger les données UI
  async function loadUI() {
    try {
      uiData.value = (await loadUIData()) as Record<string, unknown>
      isUILoaded.value = true
    } catch (error) {
      console.error('Failed to load UI data:', error)
      isUILoaded.value = false
    }
  }

  /**
   * Traduit une clé d'interface utilisateur
   * @param key - Clé de traduction (ex: 'nav.home', 'common.loading')
   * @returns Texte traduit dans la langue courante
   */
  const tUI = (key: string): string => {
    if (!uiData.value) {
      return key // Retourner la clé en attendant
    }

    // Navigue dans l'objet UI avec la clé en notation pointée
    const keys = key.split('.')
    let value: unknown = uiData.value

    for (const k of keys) {
      value = (value as Record<string, unknown>)?.[k]
      if (value === undefined) {
        console.warn(`Translation key not found: ${key}`)
        return key
      }
    }

    return typeof value === 'string' ? value : key
  }

  // Initialiser le chargement des données UI
  loadUI()

  // Recharger les données UI quand la langue change
  watch(locale, () => {
    loadUI()
  })

  return {
    tUI,
    currentLanguage: computed(() => locale.value),
    uiData: computed(() => uiData.value),
    isUILoaded: computed(() => isUILoaded.value),
  }
}
