import { ref, onMounted, onUnmounted } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { ScrollToPlugin } from 'gsap/ScrollToPlugin'

// Register GSAP plugins
if (typeof window !== 'undefined') {
  gsap.registerPlugin(ScrollTrigger, ScrollToPlugin)
}

export interface ScrollRevealOptions {
  threshold?: number
  rootMargin?: string
  animation?: string
  duration?: number
  delay?: number
  stagger?: number
  ease?: string
  once?: boolean
}

export function useScrollRevealAnimations() {
  const observedElements = ref<Map<HTMLElement, IntersectionObserver>>(new Map())
  const animationsActive = ref(true)

  // Check for reduced motion preference
  const checkReducedMotion = () => {
    if (typeof window !== 'undefined') {
      const prefersReducedMotion = window.matchMedia('(prefers-reduced-motion: reduce)').matches
      animationsActive.value = !prefersReducedMotion
    }
  }

  // Enhanced scroll reveal with GSAP
  const revealOnScroll = (
    elements: HTMLElement | HTMLElement[],
    options: ScrollRevealOptions = {}
  ) => {
    if (!animationsActive.value) return

    const {
      animation = 'fadeInUp',
      duration = 0.8,
      delay = 0,
      stagger = 0.1,
      ease = 'power2.out',
      once = true,
    } = options

    const elementsArray = Array.isArray(elements) ? elements : [elements]

    elementsArray.forEach((element, index) => {
      if (!element) return

      // Set initial state
      gsap.set(element, getInitialState(animation))

      // Create ScrollTrigger
      interface ScrollTriggerConfig {
        trigger: HTMLElement
        start: string
        end: string
        onEnter: () => void
        onLeave?: () => void
        onEnterBack?: () => void
        onLeaveBack?: () => void
      }

      const scrollTriggerConfig: ScrollTriggerConfig = {
        trigger: element,
        start: 'top 90%',
        end: 'bottom 10%',
        onEnter: () => {
          gsap.to(element, {
            ...getFinalState(animation),
            duration,
            delay: delay + index * stagger,
            ease,
          })
        },
      }

      if (!once) {
        scrollTriggerConfig.onLeave = () => {
          gsap.to(element, {
            ...getInitialState(animation),
            duration: duration * 0.5,
            ease: 'power2.in',
          })
        }
        scrollTriggerConfig.onEnterBack = () => {
          gsap.to(element, {
            ...getFinalState(animation),
            duration,
            ease,
          })
        }
        scrollTriggerConfig.onLeaveBack = () => {
          gsap.to(element, {
            ...getInitialState(animation),
            duration: duration * 0.5,
            ease: 'power2.in',
          })
        }
      }

      ScrollTrigger.create(scrollTriggerConfig)
    })
  }

  // Enhanced section transitions with gradients
  const createSectionTransition = (
    fromSection: HTMLElement,
    toSection: HTMLElement,
    transitionColor = 'rgba(59, 130, 246, 0.1)'
  ) => {
    if (!animationsActive.value) return

    // Create transition overlay
    const overlay = document.createElement('div')
    overlay.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background: linear-gradient(135deg, ${transitionColor}, transparent);
      pointer-events: none;
      z-index: 9999;
      opacity: 0;
    `
    document.body.appendChild(overlay)

    const timeline = gsap.timeline({
      onComplete: () => {
        document.body.removeChild(overlay)
      },
    })

    timeline
      .to(overlay, {
        opacity: 1,
        duration: 0.3,
        ease: 'power2.out',
      })
      .to(
        fromSection,
        {
          opacity: 0,
          y: -30,
          scale: 0.95,
          duration: 0.4,
          ease: 'power2.in',
        },
        '<'
      )
      .fromTo(
        toSection,
        {
          opacity: 0,
          y: 30,
          scale: 1.05,
        },
        {
          opacity: 1,
          y: 0,
          scale: 1,
          duration: 0.5,
          ease: 'power2.out',
        },
        '-=0.2'
      )
      .to(
        overlay,
        {
          opacity: 0,
          duration: 0.3,
          ease: 'power2.out',
        },
        '-=0.1'
      )

    return timeline
  }

  // Parallax scrolling effect
  const createParallaxScroll = (
    element: HTMLElement,
    speed = 0.5,
    direction: 'up' | 'down' = 'up'
  ) => {
    if (!animationsActive.value) return

    const factor = direction === 'up' ? -speed : speed

    ScrollTrigger.create({
      trigger: element,
      start: 'top bottom',
      end: 'bottom top',
      scrub: true,
      onUpdate: (self) => {
        const y = self.progress * 100 * factor
        gsap.set(element, { y })
      },
    })
  }

  // Smooth scroll to element - version GSAP ScrollToPlugin
  const smoothScrollTo = (target: string | HTMLElement, offset = 80) => {
    const element =
      typeof target === 'string' ? (document.querySelector(target) as HTMLElement) : target
    if (!element) {
      console.warn(`Element not found for scroll target: ${target}`)
      return
    }

    const html = document.documentElement
    const previousScrollBehavior = html.style.scrollBehavior
    html.style.scrollBehavior = 'auto'

    gsap.to(window, {
      duration: 1.2,
      scrollTo: { y: element, offsetY: offset },
      ease: 'power2.inOut',
      onComplete: () => {
        html.style.scrollBehavior = previousScrollBehavior
      },
    })
  }

  // Text reveal animation
  const revealText = (element: HTMLElement, options: ScrollRevealOptions = {}) => {
    if (!animationsActive.value) return

    const { duration = 1, delay = 0, ease = 'power2.out' } = options

    // Split text into characters
    const text = element.textContent || ''
    element.innerHTML = ''

    const chars = text.split('').map((char) => {
      const span = document.createElement('span')
      span.textContent = char === ' ' ? '\u00A0' : char
      span.style.cssText = `
        display: inline-block;
        opacity: 0;
        transform: translateY(50px) rotateX(90deg);
      `
      element.appendChild(span)
      return span
    })

    ScrollTrigger.create({
      trigger: element,
      start: 'top 80%',
      onEnter: () => {
        gsap.to(chars, {
          opacity: 1,
          y: 0,
          rotateX: 0,
          duration,
          delay,
          stagger: 0.02,
          ease,
        })
      },
    })
  }

  // Enhanced staggered cards animation
  const staggerCards = (cards: HTMLElement[], options: ScrollRevealOptions = {}) => {
    if (!animationsActive.value || cards.length === 0) return

    const { duration = 0.8, stagger = 0.15, ease = 'power2.out' } = options

    // Set initial state for all cards
    gsap.set(cards, {
      y: 100,
      opacity: 0,
      scale: 0.8,
      rotation: 5,
    })

    if (cards.length === 0) return

    const firstCard = cards[0]
    if (!firstCard) return

    ScrollTrigger.create({
      trigger: firstCard,
      start: 'top 85%',
      onEnter: () => {
        gsap.to(cards, {
          y: 0,
          opacity: 1,
          scale: 1,
          rotation: 0,
          duration,
          stagger,
          ease,
        })
      },
    })
  }

  // Morphing section backgrounds
  const morphSectionBackground = (section: HTMLElement, colors: string[], duration = 8) => {
    if (!animationsActive.value || colors.length < 2) return

    let currentIndex = 0

    const animateBackground = () => {
      const nextIndex = (currentIndex + 1) % colors.length

      gsap.to(section, {
        background: `linear-gradient(135deg, ${colors[currentIndex]}, ${colors[nextIndex]})`,
        duration: duration / colors.length,
        ease: 'sine.inOut',
        onComplete: () => {
          currentIndex = nextIndex
          animateBackground()
        },
      })
    }

    // Start when section is in view
    ScrollTrigger.create({
      trigger: section,
      start: 'top center',
      end: 'bottom center',
      onEnter: animateBackground,
      onLeave: () => gsap.killTweensOf(section),
    })
  }

  // Get initial animation state
  const getInitialState = (animation: string) => {
    switch (animation) {
      case 'fadeInUp':
        return { opacity: 0, y: 50 }
      case 'fadeInDown':
        return { opacity: 0, y: -50 }
      case 'fadeInLeft':
        return { opacity: 0, x: -50 }
      case 'fadeInRight':
        return { opacity: 0, x: 50 }
      case 'scaleIn':
        return { opacity: 0, scale: 0.8 }
      case 'rotateIn':
        return { opacity: 0, rotation: -90, scale: 0.8 }
      case 'slideInUp':
        return { opacity: 0, y: 100, skewY: 5 }
      case 'bounceIn':
        return { opacity: 0, scale: 0.3 }
      case 'flipInX':
        return { opacity: 0, rotationX: -90 }
      case 'flipInY':
        return { opacity: 0, rotationY: -90 }
      default:
        return { opacity: 0, y: 30 }
    }
  }

  // Get final animation state
  const getFinalState = (animation: string) => {
    switch (animation) {
      case 'fadeInUp':
      case 'fadeInDown':
        return { opacity: 1, y: 0 }
      case 'fadeInLeft':
      case 'fadeInRight':
        return { opacity: 1, x: 0 }
      case 'scaleIn':
        return { opacity: 1, scale: 1 }
      case 'rotateIn':
        return { opacity: 1, rotation: 0, scale: 1 }
      case 'slideInUp':
        return { opacity: 1, y: 0, skewY: 0 }
      case 'bounceIn':
        return { opacity: 1, scale: 1 }
      case 'flipInX':
        return { opacity: 1, rotationX: 0 }
      case 'flipInY':
        return { opacity: 1, rotationY: 0 }
      default:
        return { opacity: 1, y: 0 }
    }
  }

  onMounted(() => {
    checkReducedMotion()

    // Listen for changes in motion preference
    if (typeof window !== 'undefined') {
      const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)')
      mediaQuery.addEventListener('change', checkReducedMotion)
    }
  })

  onUnmounted(() => {
    // Clean up ScrollTriggers
    ScrollTrigger.getAll().forEach((trigger) => trigger.kill())

    // Clean up observers
    observedElements.value.forEach((observer, element) => {
      observer.unobserve(element)
      observer.disconnect()
    })
    observedElements.value.clear()
  })

  return {
    animationsActive,
    revealOnScroll,
    createSectionTransition,
    createParallaxScroll,
    smoothScrollTo,
    revealText,
    staggerCards,
    morphSectionBackground,
  }
}
