<template>
  <div class="relative" ref="languageSwitcher">
    <!-- Language Toggle Button -->
    <button
      @click="toggleDropdown"
      @keydown.escape="closeDropdown"
      :aria-expanded="isOpen"
      aria-haspopup="true"
      :aria-label="currentLanguage === 'fr' ? 'Changer de langue' : 'Change language'"
      class="group relative flex items-center space-x-2 rounded-lg border border-transparent bg-gray-100/50 p-2 backdrop-blur-sm transition-all duration-300 hover:border-blue-500/20 hover:bg-gray-200/50 dark:bg-gray-800/50 dark:hover:bg-gray-700/50"
    >
      <!-- Current Language Flag -->
      <div
        class="relative flex h-6 w-6 items-center justify-center overflow-hidden rounded-sm transition-transform duration-300 group-hover:scale-110"
      >
        <span
          :class="['fi h-6 w-6 rounded-sm', currentLanguage === 'fr' ? 'fi-fr' : 'fi-gb']"
          :aria-label="currentLanguage === 'fr' ? 'Français' : 'English'"
        ></span>
      </div>

      <!-- Chevron Icon -->
      <FontAwesomeIcon
        icon="chevron-down"
        :class="[
          'h-3 w-3 text-gray-500 transition-transform duration-300 dark:text-gray-400',
          isOpen ? 'rotate-180' : '',
        ]"
      />
    </button>

    <!-- Dropdown Menu -->
    <Transition
      enter-active-class="transition ease-out duration-200"
      enter-from-class="opacity-0 scale-95 translate-y-1"
      enter-to-class="opacity-100 scale-100 translate-y-0"
      leave-active-class="transition ease-in duration-150"
      leave-from-class="opacity-100 scale-100 translate-y-0"
      leave-to-class="opacity-0 scale-95 translate-y-1"
    >
      <div
        v-if="isOpen"
        class="absolute right-0 top-full z-50 mt-2 w-48 rounded-lg border border-gray-200 bg-white py-1 shadow-lg backdrop-blur-sm dark:border-gray-700 dark:bg-gray-800"
        role="menu"
        aria-orientation="vertical"
      >
        <button
          v-for="language in languages"
          :key="language.code"
          @click="selectLanguage(language.code)"
          :class="[
            'flex w-full items-center space-x-3 px-4 py-2 text-left transition-colors duration-200 hover:bg-gray-50 dark:hover:bg-gray-700',
            currentLanguage === language.code
              ? 'bg-blue-50 text-blue-600 dark:bg-blue-900/20 dark:text-blue-400'
              : 'text-gray-700 dark:text-gray-300',
          ]"
          role="menuitem"
        >
          <!-- Language Icon -->
          <div class="flex h-4 w-5 flex-shrink-0 items-center justify-center">
            <span
              :class="['fi h-4 w-5 rounded-sm', language.code === 'fr' ? 'fi-fr' : 'fi-gb']"
              :aria-label="language.code === 'fr' ? 'Français' : 'English'"
            ></span>
          </div>

          <!-- Language Name -->
          <span class="flex-1 font-medium">{{ language.name }}</span>

          <!-- Check Icon for current language -->
          <FontAwesomeIcon
            v-if="currentLanguage === language.code"
            icon="check"
            class="h-4 w-4 text-blue-600 dark:text-blue-400"
          />
        </button>
      </div>
    </Transition>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useI18n } from 'vue-i18n'

// Interface pour les langues
interface Language {
  code: string
  name: string
  icon: string
}

// État du composant
const isOpen = ref(false)
const languageSwitcher = ref<HTMLElement>()

// Vue I18n
const { locale } = useI18n()

// Langues disponibles avec leurs icônes FontAwesome
const languages = ref<Language[]>([
  {
    code: 'fr',
    name: 'Français',
    icon: 'globe', // On peut utiliser 'globe' ou créer une icône custom
  },
  {
    code: 'en',
    name: 'English',
    icon: 'globe', // On peut utiliser 'globe' ou créer une icône custom
  },
])

// Langue actuelle
const currentLanguage = computed(() => locale.value)

// Fonctions
function toggleDropdown() {
  isOpen.value = !isOpen.value
}

function closeDropdown() {
  isOpen.value = false
}

function selectLanguage(languageCode: string) {
  if (languageCode !== currentLanguage.value) {
    // Changer la langue
    locale.value = languageCode

    // Persister dans localStorage
    localStorage.setItem('portfolio-language', languageCode)

    // Émettre un événement pour notifier d'autres composants si nécessaire
    window.dispatchEvent(
      new CustomEvent('language-changed', {
        detail: { language: languageCode },
      })
    )
  }

  closeDropdown()
}

// Fermer le dropdown en cliquant à l'extérieur
function handleClickOutside(event: Event) {
  if (languageSwitcher.value && !languageSwitcher.value.contains(event.target as Node)) {
    closeDropdown()
  }
}

// Gestion des événements clavier pour l'accessibilité
function handleKeydown(event: KeyboardEvent) {
  if (isOpen.value) {
    switch (event.key) {
      case 'Escape':
        closeDropdown()
        break
      case 'ArrowDown':
        event.preventDefault()
        // Focus sur le premier élément du menu
        break
      case 'ArrowUp':
        event.preventDefault()
        // Focus sur le dernier élément du menu
        break
    }
  }
}

// Lifecycle
onMounted(() => {
  document.addEventListener('click', handleClickOutside)
  document.addEventListener('keydown', handleKeydown)
})

onUnmounted(() => {
  document.removeEventListener('click', handleClickOutside)
  document.removeEventListener('keydown', handleKeydown)
})
</script>

<style scoped>
/* Animations personnalisées pour le dropdown */
.dropdown-enter-active {
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.dropdown-leave-active {
  transition: all 0.15s cubic-bezier(0.4, 0, 0.2, 1);
}

.dropdown-enter-from,
.dropdown-leave-to {
  opacity: 0;
  transform: scale(0.95) translateY(-4px);
}

/* Style pour les drapeaux */
img {
  border-radius: 2px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

/* Focus visible pour l'accessibilité */
button:focus-visible {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Support pour reduced motion */
@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
  }
}
</style>
