import { ref, computed, watch } from 'vue'
import { useI18n } from 'vue-i18n'
import {
  FALLBACK_LOCALE,
  SUPPORTED_LOCALES,
  type SupportedLocale,
  localizedContent,
} from '@/data/localized-content'
import { getSettingsForLocale } from '@/data/site-settings'

// Cache pour éviter de recharger les mêmes données - GLOBAL et partagé
const dataCache = new Map<string, unknown>()
// Tracker pour vider le cache au changement de langue
let lastLocale: string | null = null

export function useLocalizedData() {
  const { locale } = useI18n()
  const loading = ref(false)
  const error = ref<string | null>(null)

  // Vérifier si la locale a changé et vider le cache si nécessaire
  if (locale.value !== lastLocale && lastLocale !== null) {
    dataCache.clear()
  }
  lastLocale = locale.value

  // Fonction pour charger des données selon la langue
  async function loadData<T>(fileName: string): Promise<T | null> {
    const cacheKey = `${fileName}-${locale.value}`

    if (dataCache.has(cacheKey)) {
      return dataCache.get(cacheKey) as T
    }

    loading.value = true
    error.value = null

    const normalizedLocale = SUPPORTED_LOCALES.includes(locale.value as SupportedLocale)
      ? (locale.value as SupportedLocale)
      : FALLBACK_LOCALE

    try {
      let data: unknown | null = null

      if (fileName === 'settings') {
        data = getSettingsForLocale(normalizedLocale) ?? null
      } else {
        const sectionBucket = localizedContent[fileName]

        if (sectionBucket) {
          const localizedValue = sectionBucket[normalizedLocale]
          const fallbackValue = sectionBucket[FALLBACK_LOCALE]

          if (localizedValue != null) {
            data = localizedValue
          } else if (fallbackValue != null) {
            data = fallbackValue
          }
        }
      }

      if (data == null) {
        throw new Error(`Missing localized data for ${fileName}`)
      }

      dataCache.set(cacheKey, data)
      return data as T
    } catch (err) {
      const message = err instanceof Error ? err.message : `Failed to load ${fileName}`
      error.value = message
      console.error(message)
      return null
    } finally {
      loading.value = false
    }
  }

  // Créer une fonction qui retourne une ref réactive basée sur la locale
  function createLocalizedRef<T>(fileName: string, defaultValue: T) {
    const data = ref<T>(defaultValue)

    // Charger initialement
    const loadAndUpdate = async () => {
      const newData = await loadData<T>(fileName)
      if (newData !== null) {
        data.value = newData
      }
    }

    // Charger lors de l'initialisation
    loadAndUpdate()

    // Recharger quand la langue change - Vider le cache d'abord avant de recharger
    watch(locale, () => {
      clearCache()
      loadAndUpdate()
    }, { immediate: false })

    return data
  }

  // Fonction pour charger plusieurs fichiers
  async function loadMultipleData<T extends Record<string, unknown>>(
    fileNames: (keyof T)[]
  ): Promise<Partial<T>> {
    const results: Partial<T> = {}

    await Promise.all(
      fileNames.map(async (fileName) => {
        const data = await loadData(fileName as string)
        if (data) {
          ;(results as Record<string, unknown>)[fileName as string] = data
        }
      })
    )

    return results
  }

  // Fonction pour vider le cache (utile lors du changement de langue)
  function clearCache() {
    dataCache.clear()
  }

  // Fonctions spécialisées pour chaque type de données
  const loadHeroData = () => loadData<unknown>('hero')
  const loadExperienceData = () => loadData<unknown[]>('experience')
  const loadProjectsData = () => loadData<unknown[]>('projects')
  const loadSkillsData = () => loadData<unknown>('skills')
  const loadTestimonialsData = () => loadData<unknown[]>('testimonials')
  const loadSettingsData = () => loadData<unknown>('settings')
  const loadUIData = () => loadData<unknown>('ui')
  const loadSiteFooterData = () => loadData<unknown>('site-footer')

  return {
    loading: computed(() => loading.value),
    error: computed(() => error.value),
    loadData,
    loadMultipleData,
    clearCache,
    createLocalizedRef,
    // Fonctions spécialisées
    loadHeroData,
    loadExperienceData,
    loadProjectsData,
    loadSkillsData,
    loadTestimonialsData,
    loadSettingsData,
    loadUIData,
    loadSiteFooterData,
  }
}
