<template>
  <div :class="['flex gap-4', directionClasses, alignmentClasses]">
    <a
      v-for="link in socialLinks"
      :key="link.name"
      :href="link.url"
      :aria-label="`${link.name} de ${name || 'l\'auteur'}`"
      target="_blank"
      rel="noopener noreferrer"
      :class="[
        'inline-flex items-center justify-center transition-all duration-200',
        'hover:scale-110 focus:scale-110 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2',
        'dark:focus:ring-offset-gray-900',
        sizeClasses,
        roundedClasses,
        link.color ? '' : defaultColorClasses,
      ]"
      :style="link.color ? `color: ${link.color}; border-color: ${link.color}20` : ''"
      @mouseenter="onHover(link.name)"
      @focus="onFocus(link.name)"
    >
      <FontAwesomeIcon
        v-if="link.icon.startsWith('fab fa-')"
        :icon="['fab', link.icon.replace('fab fa-', '')]"
        :size="iconSize"
        class="transition-transform duration-200 group-hover:scale-110"
      />
      <FontAwesomeIcon
        v-else-if="link.icon.startsWith('fas fa-')"
        :icon="link.icon.replace('fas fa-', '')"
        :size="iconSize"
        class="transition-transform duration-200 group-hover:scale-110"
      />
      <Icon
        v-else
        :name="link.icon"
        :size="iconSize"
        class="transition-transform duration-200 group-hover:scale-110"
      />
      <span v-if="showLabels" class="ml-2 text-sm font-medium">
        {{ link.label || link.name }}
      </span>
    </a>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useSocialLinksStore } from '@/stores/social-links'
import Icon from './Icon.vue'

export interface SocialLink {
  name: string
  label?: string
  url: string
  icon: string
  color?: string
}

export interface SocialLinksProps {
  links?: SocialLink[]
  name?: string
  size?: 'sm' | 'md' | 'lg'
  variant?: 'minimal' | 'bordered' | 'filled'
  direction?: 'horizontal' | 'vertical'
  alignment?: 'start' | 'center' | 'end'
  showLabels?: boolean
  useStore?: boolean // Nouvelle prop pour utiliser le store
}

const props = withDefaults(defineProps<SocialLinksProps>(), {
  size: 'md',
  variant: 'minimal',
  direction: 'horizontal',
  alignment: 'center',
  showLabels: false,
  useStore: true, // Par défaut, utiliser le store
})

const emit = defineEmits<{
  hover: [platform: string]
  focus: [platform: string]
}>()

const socialLinksStore = useSocialLinksStore()

// Liens sociaux par défaut (fallback si le store n'est pas utilisé)
const defaultSocialLinks: SocialLink[] = [
  {
    name: 'GitHub',
    url: 'https://github.com/fallback',
    icon: 'fab fa-github',
    color: '#181717',
  },
  {
    name: 'LinkedIn',
    url: 'https://linkedin.com/in/fallback',
    icon: 'fab fa-linkedin',
    color: '#0A66C2',
  },
  {
    name: 'Twitter',
    url: 'https://twitter.com/fallback',
    icon: 'fab fa-twitter',
    color: '#1DA1F2',
  },
  {
    name: 'Email',
    url: 'mailto:contact@fallback.com',
    icon: 'fas fa-envelope',
    color: '#EA4335',
  },
]

const socialLinks = computed(() => {
  if (props.useStore) {
    // Transformer les données du store au format attendu par le composant
    return socialLinksStore.enabledSocialLinks.map((link) => ({
      name: link.label,
      label: link.label,
      url: link.url,
      icon: link.icon,
      color: undefined, // Pas de couleur customisée depuis le store pour l'instant
    }))
  }
  return props.links || defaultSocialLinks
})

// Classes de direction
const directionClasses = computed(() => {
  return props.direction === 'vertical' ? 'flex-col' : 'flex-row flex-wrap'
})

// Classes d'alignement
const alignmentClasses = computed(() => {
  const alignments = {
    start: props.direction === 'vertical' ? 'items-start' : 'justify-start',
    center: props.direction === 'vertical' ? 'items-center' : 'justify-center',
    end: props.direction === 'vertical' ? 'items-end' : 'justify-end',
  }
  return alignments[props.alignment]
})

// Classes de taille
const sizeClasses = computed(() => {
  const sizes = {
    sm: 'w-8 h-8 p-1.5',
    md: 'w-10 h-10 p-2',
    lg: 'w-12 h-12 p-2.5',
  }
  return sizes[props.size]
})

// Taille des icônes
const iconSize = computed(() => {
  const iconSizes = {
    sm: 'sm' as const,
    md: 'sm' as const,
    lg: 'lg' as const,
  }
  return iconSizes[props.size]
})

// Classes de bordure
const roundedClasses = computed(() => {
  return props.variant === 'minimal' ? 'rounded-md' : 'rounded-full'
})

// Classes de couleur par défaut
const defaultColorClasses = computed(() => {
  const variants = {
    minimal: 'text-gray-600 hover:text-primary-600 dark:text-gray-400 dark:hover:text-primary-400',
    bordered:
      'text-gray-600 hover:text-primary-600 dark:text-gray-400 dark:hover:text-primary-400 border border-gray-300 hover:border-primary-600 dark:border-gray-600 dark:hover:border-primary-400',
    filled:
      'text-white bg-gray-600 hover:bg-primary-600 dark:bg-gray-700 dark:hover:bg-primary-500',
  }
  return variants[props.variant]
})

// Gestionnaires d'événements
const onHover = (platform: string) => {
  emit('hover', platform)
}

const onFocus = (platform: string) => {
  emit('focus', platform)
}
</script>
